import React from "react"
import { Link } from "@atoms/Link"

export default function Menu({callback = () => {}})
{
    let menu = [
        {
            label: "",
            url: '/',
            scroll: '#top'
        }, {
            label: "",
            url: '/',
            scroll: '#about'
        }, {
            label: "TOPICKS",
            url: '/',
            scroll: '#topicks'
        }, {
            label: "",
            url: '/',
            scroll: '#instructors'
        }, {
            label: "",
            url: '/',
            scroll: '#lessons'
        }, {
            label: "",
            url: '/',
            scroll: '#movies'
        }, {
            label: "",
            url: '/',
            scroll: '#interviews'
        }, {
            label: "",
            url: '/',
            scroll: '#audition'
        }, {
            label: "",
            url: '/',
            scroll: '#message'
        }, {
            label: "",
            url: '/',
            scroll: '#access'
        }, {
            isSpace: true,
        }, {
            label: "",
            url: "/privacy-policy/",
            className: 'small'
        }, {
            label: "",
            url: "/ebizrule/",
            className: 'small'
        }
    ]
    return(
        <nav>
            <ul>
                {menu.map((val, i) => {
                    let { label, url, isSpace, className, scroll } = val
                    if (isSpace) {
                        return (
                            <li key={i} className="space">
                            </li>
                        )
                    } else {
                        if( !url ){
                            return (
                                <li key={i} className={className || ''}>
                                    <Link scroll={scroll} onClick={callback}>{label}</Link>
                                </li>
                            )
                        } else {
                            return (
                                <li key={i} className={className || ''}>
                                    <Link to={url} scroll={scroll} onClick={callback}>{label}</Link>
                                </li>
                            )
                        }
                    }
                })}
            </ul>
        </nav>
    )
}
